import { request } from '../../request.js'
export default {
	data() {
		const date = new Date()
		const years = []
		const year = date.getFullYear()
		const months = []
		const month = date.getMonth() + 1
		for (let i = 1990; i <= date.getFullYear(); i++) {
			years.push(i)
		}
		for (let i = 1; i <= 12; i++) {
			months.push(i)
		}
		return {
			page:1,
			years,
			year,
			type : ["余额流水","折扣券流水","现金券流水"],
			months,
			month,
			expense:0,
			income:0,
			list:[],
			chooseYear:'',
			chooseMonth:'',
			chooseType:"",
			sType:"",
			value: [9999, month - 1],
			visible: false,
			visible_type:false,
			resType:1,
			indicatorStyle: `text-align:center;width:100%;height: ${Math.round(uni.getSystemInfoSync().screenWidth/(750/100))}px;`,
			$src:this.$src
		};
	},
	onLoad:function(){
		this.getList();
		this.sType=this.type[0];
	},
	methods:{
		openView:function(){
			console.log(this.years);
			this.visible = true;
		},
		openType:function(){
			this.visible_type=true;
		},
		bindChange: function(e) {
			const val = e.detail.value
			this.chooseYear = this.years[val[0]]
			this.chooseMonth = this.months[val[1]]
		},
		typeBindChange:function(e){
			const val = e.detail.value
			this.chooseType = this.type[val[0]]
		},
		
		typeChangeCancel:function(){
			this.visible_type = false;
		},
		changeOk:function(){
			this.year = this.chooseYear;
			this.month = this.chooseMonth;
			if(this.resType==1){
				this.getList();
			}else if (this.resType ==3){
				this.getList2();
			}else{
				this.getList2();
			}
			this.changeCancel();
		},
		typeChangeOk:function(){
			this.sType = this.chooseType;
			console.log(this.sType);
			this.typeChangeCancel();
			if(this.sType == "余额流水"){
				this.resType=1;
				this.getList();
			}else if(this.sType == "折扣券流水"){
				this.resType=3;
				this.getList2();
			}else{
				this.resType=4;
				this.getList2();
			}
			
			
		},
		changeCancel:function(){
			this.visible = false;
		},
		getList:function(){
			let that = this;
			request({
				url: 'index.php?s=/wap/member/apiBalanceList',
				data:{
					year:that.year,
					month:that.month,
				},
				method:"POST",
			}).then(function(res){
				console.log(res);
				that.list = res.data.list;
				that.expense = res.data.expense;
				that.income = res.data.income;
			});
		},
		getList2:function(){
			let that = this;
			request({
				url: 'index.php?s=/wap/member/Apin_money',
				data:{
					year:that.year,
					month:that.month,
					type:that.resType,
				},
				method:"POST",
			}).then(function(res){
				console.log(res);
				that.list = res.data.list;
				that.expense = res.data.expense;
				that.income = res.data.income;
			});
		}
	}
}
